/* Reset */
/* ========================================================================== */

*, *::before, *::after {
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

  --standard-padding: 16px; 
}

img {
  max-width: 100%;
}

/* ========================================================================== */
/* Main Styling */
/* ========================================================================== */

main {
  width: min(1150px, 100%);
  margin: auto;
  padding: var(--standard-padding);
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 6px ridge lightgray;
}

form .error-msg {
  min-height: 1.5rem;
  white-space: pre;
  color: red;
}

form .loading-container {
  display: inline-block;
  width: 16px;
  height: 16px;
}

#content {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--standard-padding);
}

.weather-card {
  /* flex: auto; */
  /* width: 360px; */
  padding: var(--standard-padding);
  border: 1px solid black;
}

.weather-card .date {
  font-weight: bold;
}

.weather-card .middle {
  display: grid;
  grid-template: 1fr / repeat(2, 1fr);
}

.weather-card .stat-header {
  color: gray;
}

.weather-card .icon-container {
  display: grid;
  justify-content: center;
  align-content: center;
}

.weather-card .stats {
  display: grid;
  grid-template: repeat(2, 1fr) / repeat(2, 1fr);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,+EAA+E;AAC/E,UAAU;AACV,+EAA+E;;AAE/E;EACE,sBAAsB;EACtB,mJAAmJ;;EAEnJ,wBAAwB;AAC1B;;AAEA;EACE,eAAe;AACjB;;AAEA,+EAA+E;AAC/E,iBAAiB;AACjB,+EAA+E;;AAE/E;EACE,wBAAwB;EACxB,YAAY;EACZ,gCAAgC;EAChC,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,2BAA2B;AAC7B;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,UAAU;AACZ;;AAEA;EACE,qBAAqB;EACrB,WAAW;EACX,YAAY;AACd;;AAEA;EACE,aAAa;EACb,WAAW;EACX,2DAA2D;EAC3D,4BAA4B;AAC9B;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,gCAAgC;EAChC,uBAAuB;AACzB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mCAAmC;AACrC;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,8CAA8C;AAChD","sourcesContent":["/* ========================================================================== */\n/* Reset */\n/* ========================================================================== */\n\n*, *::before, *::after {\n  box-sizing: border-box;\n  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n\n  --standard-padding: 16px; \n}\n\nimg {\n  max-width: 100%;\n}\n\n/* ========================================================================== */\n/* Main Styling */\n/* ========================================================================== */\n\nmain {\n  width: min(1150px, 100%);\n  margin: auto;\n  padding: var(--standard-padding);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  border: 6px ridge lightgray;\n}\n\nform .error-msg {\n  min-height: 1.5rem;\n  white-space: pre;\n  color: red;\n}\n\nform .loading-container {\n  display: inline-block;\n  width: 16px;\n  height: 16px;\n}\n\n#content {\n  display: grid;\n  width: 100%;\n  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n  gap: var(--standard-padding);\n}\n\n.weather-card {\n  /* flex: auto; */\n  /* width: 360px; */\n  padding: var(--standard-padding);\n  border: 1px solid black